import { useState, useEffect } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from './ui/card'
import { Button } from './ui/button'
import { Input } from './ui/input'
import { Label } from './ui/label'
import { Textarea } from './ui/textarea'
import { VagaFormData, Vaga, Report } from '../types/database'
import { getVagaById, updateVaga } from '../lib/vagas'
import { getReportById, updateReportStatus } from '../lib/reports'
import { ArrowLeft, Save, Loader2, AlertCircle, CheckCircle } from 'lucide-react'

export default function EditarVagaFromReport() {
  const { id } = useParams<{ id: string }>()
  const navigate = useNavigate()
  const { user } = useAuth()
  const [formData, setFormData] = useState<VagaFormData>({
    site: '',
    categoria: '',
    cargo: '',
    cliente: '',
    titulo: '',
    celula: '',
    descricao_vaga: '',
    responsabilidades_atribuicoes: '',
    requisitos_qualificacoes: '',
    salario: '',
    horario_trabalho: '',
    jornada_trabalho: '',
    beneficios: '',
    local_trabalho: '',
    etapas_processo: ''
  })
  const [loading, setLoading] = useState(false)
  const [loadingData, setLoadingData] = useState(true)
  const [message, setMessage] = useState('')
  const [report, setReport] = useState<Report | null>(null)
  const [editableField, setEditableField] = useState<string>('')
  const [originalValue, setOriginalValue] = useState<string>('')

  useEffect(() => {
    const loadData = async () => {
      if (!id) return

      try {
        setLoadingData(true)
        
        // Buscar o report
        const reportData = await getReportById(id)
        if (!reportData) {
          setMessage('Report n√£o encontrado')
          return
        }

        setReport(reportData)
        setEditableField(reportData.field_name)

        // Buscar a vaga
        const vagaData = await getVagaById(reportData.vaga_id)
        if (!vagaData) {
          setMessage('Vaga n√£o encontrada')
          return
        }

        // Preencher formul√°rio com dados da vaga
        setFormData({
          site: vagaData.site || '',
          categoria: vagaData.categoria || '',
          cargo: vagaData.cargo || '',
          cliente: vagaData.cliente || '',
          titulo: vagaData.titulo || '',
          celula: vagaData.celula || '',
          descricao_vaga: vagaData.descricao_vaga || '',
          responsabilidades_atribuicoes: vagaData.responsabilidades_atribuicoes || '',
          requisitos_qualificacoes: vagaData.requisitos_qualificacoes || '',
          salario: vagaData.salario || '',
          horario_trabalho: vagaData.horario_trabalho || '',
          jornada_trabalho: vagaData.jornada_trabalho || '',
          beneficios: vagaData.beneficios || '',
          local_trabalho: vagaData.local_trabalho || '',
          etapas_processo: vagaData.etapas_processo || ''
        })

        // Salvar valor original do campo edit√°vel
        const originalFieldValue = (vagaData as any)[reportData.field_name] || ''
        setOriginalValue(originalFieldValue)

      } catch (error: any) {
        console.error('Erro ao carregar dados:', error)
        setMessage(`Erro ao carregar dados: ${error.message}`)
      } finally {
        setLoadingData(false)
      }
    }

    loadData()
  }, [id])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (!user || !id || !report) return

    setLoading(true)
    setMessage('')

    try {
      // Atualizar a vaga
      const vagaAtualizada = await updateVaga(report.vaga_id, formData, user.id)
      
      if (vagaAtualizada) {
        // Marcar report como conclu√≠do
        await updateReportStatus(id, 'completed', 'Altera√ß√£o realizada com sucesso')
        
        setMessage('Altera√ß√£o realizada com sucesso!')
        
        // Mostrar popup de sucesso e redirecionar
        setTimeout(() => {
          alert('Altera√ß√£o realizada com sucesso!')
          navigate('/dashboard/clientes')
        }, 1000)
      } else {
        setMessage('Erro ao atualizar vaga')
      }
    } catch (error: any) {
      console.error('Erro detalhado ao atualizar vaga:', error)
      const errorMessage = error?.message || 'Erro ao atualizar vaga'
      setMessage(`Erro ao atualizar vaga: ${errorMessage}`)
    } finally {
      setLoading(false)
    }
  }

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
  }

  const isFieldEditable = (fieldName: string) => {
    return fieldName === editableField
  }

  const getFieldLabel = (fieldName: string) => {
    const labels: { [key: string]: string } = {
      site: 'Site',
      categoria: 'Categoria',
      cargo: 'Cargo',
      cliente: 'Cliente',
      titulo: 'T√≠tulo',
      descricao_vaga: 'Descri√ß√£o da vaga',
      responsabilidades_atribuicoes: 'Responsabilidades e atribui√ß√µes',
      requisitos_qualificacoes: 'Requisitos e qualifica√ß√µes',
      salario: 'Sal√°rio',
      horario_trabalho: 'Hor√°rio de Trabalho',
      jornada_trabalho: 'Jornada de Trabalho',
      beneficios: 'Benef√≠cios',
      local_trabalho: 'Local de Trabalho',
      etapas_processo: 'Etapas do processo'
    }
    return labels[fieldName] || fieldName
  }

  if (loadingData) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <Loader2 className="h-12 w-12 animate-spin text-blue-600" />
        <p className="ml-3 text-lg text-gray-700">Carregando dados do report...</p>
      </div>
    )
  }

  if (message && message.includes('n√£o encontrado')) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen text-red-600">
        <p className="text-xl mb-4">{message}</p>
        <Button onClick={() => navigate('/dashboard')} variant="outline">
          <ArrowLeft className="h-4 w-4 mr-2" /> Voltar para o dashboard
        </Button>
      </div>
    )
  }

  if (!report) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen text-red-600">
        <p className="text-xl mb-4">Report n√£o encontrado</p>
        <Button onClick={() => navigate('/dashboard')} variant="outline">
          <ArrowLeft className="h-4 w-4 mr-2" /> Voltar para o dashboard
        </Button>
      </div>
    )
  }

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold flex items-center gap-2">
            <AlertCircle className="h-6 w-6 text-orange-500" />
            Editar Oportunidade - Report
          </h1>
          <p className="text-gray-600 mt-1">
            Campo reportado: <span className="font-semibold">{getFieldLabel(editableField)}</span>
          </p>
        </div>
        <Button onClick={() => navigate('/dashboard/clientes')} variant="outline">
          <ArrowLeft className="h-4 w-4 mr-2" />
          Voltar
        </Button>
      </div>

      {/* Informa√ß√µes do Report */}
      <Card className="border-orange-200 bg-orange-50 dark:bg-orange-900/20">
        <CardHeader>
          <CardTitle className="text-sm flex items-center gap-2">
            <AlertCircle className="h-4 w-4 text-orange-500" />
            Informa√ß√µes do Report
          </CardTitle>
        </CardHeader>
        <CardContent className="space-y-3">
          <div>
            <Label className="text-sm font-medium">Campo Reportado:</Label>
            <p className="text-sm text-gray-700">{getFieldLabel(editableField)}</p>
          </div>
          <div>
            <Label className="text-sm font-medium">Valor Atual:</Label>
            <div className="bg-gray-100 dark:bg-gray-800 p-2 rounded text-sm">
              {originalValue || 'N√£o informado'}
            </div>
          </div>
          <div>
            <Label className="text-sm font-medium">Sugest√µes do RH:</Label>
            <div className="bg-blue-50 dark:bg-blue-900/20 p-2 rounded text-sm">
              {report.suggested_changes}
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Mensagem de Status */}
      {message && (
        <div className={`p-4 rounded-md ${
          message.includes('sucesso') 
            ? 'bg-green-50 text-green-700 border border-green-200' 
            : 'bg-red-50 text-red-700 border border-red-200'
        }`}>
          {message}
        </div>
      )}

      {/* Formul√°rio */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <CheckCircle className="h-5 w-5 text-green-500" />
            Informa√ß√µes da Oportunidade
          </CardTitle>
          <CardDescription>
            Apenas o campo reportado pode ser editado. Os demais campos s√£o apenas para visualiza√ß√£o.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Informa√ß√µes B√°sicas */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="site">Site</Label>
                <Input
                  id="site"
                  name="site"
                  value={formData.site}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('site')}
                  className={isFieldEditable('site') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="categoria">Categoria</Label>
                <Input
                  id="categoria"
                  name="categoria"
                  value={formData.categoria}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('categoria')}
                  className={isFieldEditable('categoria') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="cargo">Cargo</Label>
                <Input
                  id="cargo"
                  name="cargo"
                  value={formData.cargo}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('cargo')}
                  className={isFieldEditable('cargo') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="cliente">Cliente</Label>
                <Input
                  id="cliente"
                  name="cliente"
                  value={formData.cliente}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('cliente')}
                  className={isFieldEditable('cliente') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="titulo">T√≠tulo</Label>
                <Input
                  id="titulo"
                  name="titulo"
                  value={formData.titulo}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('titulo')}
                  className={isFieldEditable('titulo') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="celula">C√©lula</Label>
                <Input
                  id="celula"
                  name="celula"
                  value={formData.celula}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('celula')}
                  className={isFieldEditable('celula') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                />
              </div>
            </div>

            {/* Campos de Texto */}
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="descricao_vaga">Descri√ß√£o da Vaga</Label>
                <Textarea
                  id="descricao_vaga"
                  name="descricao_vaga"
                  value={formData.descricao_vaga}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('descricao_vaga')}
                  className={isFieldEditable('descricao_vaga') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  rows={3}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="responsabilidades_atribuicoes">Responsabilidades e Atribui√ß√µes</Label>
                <Textarea
                  id="responsabilidades_atribuicoes"
                  name="responsabilidades_atribuicoes"
                  value={formData.responsabilidades_atribuicoes}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('responsabilidades_atribuicoes')}
                  className={isFieldEditable('responsabilidades_atribuicoes') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  rows={3}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="requisitos_qualificacoes">Requisitos e Qualifica√ß√µes</Label>
                <Textarea
                  id="requisitos_qualificacoes"
                  name="requisitos_qualificacoes"
                  value={formData.requisitos_qualificacoes}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('requisitos_qualificacoes')}
                  className={isFieldEditable('requisitos_qualificacoes') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  rows={3}
                />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="salario">Sal√°rio</Label>
                  <Input
                    id="salario"
                    name="salario"
                    value={formData.salario}
                    onChange={handleInputChange}
                    disabled={!isFieldEditable('salario')}
                    className={isFieldEditable('salario') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="horario_trabalho">Hor√°rio de Trabalho</Label>
                  <Input
                    id="horario_trabalho"
                    name="horario_trabalho"
                    value={formData.horario_trabalho}
                    onChange={handleInputChange}
                    disabled={!isFieldEditable('horario_trabalho')}
                    className={isFieldEditable('horario_trabalho') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="jornada_trabalho">Jornada de Trabalho</Label>
                  <Input
                    id="jornada_trabalho"
                    name="jornada_trabalho"
                    value={formData.jornada_trabalho}
                    onChange={handleInputChange}
                    disabled={!isFieldEditable('jornada_trabalho')}
                    className={isFieldEditable('jornada_trabalho') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  />
                </div>
              </div>
              <div className="space-y-2">
                <Label htmlFor="beneficios">Benef√≠cios</Label>
                <Textarea
                  id="beneficios"
                  name="beneficios"
                  value={formData.beneficios}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('beneficios')}
                  className={isFieldEditable('beneficios') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  rows={3}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="local_trabalho">Local de Trabalho</Label>
                <Textarea
                  id="local_trabalho"
                  name="local_trabalho"
                  value={formData.local_trabalho}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('local_trabalho')}
                  className={isFieldEditable('local_trabalho') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  rows={2}
                />
              </div>
              <div className="space-y-2">
                <Label htmlFor="etapas_processo">Etapas do Processo</Label>
                <Textarea
                  id="etapas_processo"
                  name="etapas_processo"
                  value={formData.etapas_processo}
                  onChange={handleInputChange}
                  disabled={!isFieldEditable('etapas_processo')}
                  className={isFieldEditable('etapas_processo') ? '' : 'bg-gray-100 dark:bg-gray-800'}
                  rows={3}
                />
              </div>
            </div>

            {/* Bot√µes */}
            <div className="flex justify-end gap-3 pt-6">
              <Button
                type="button"
                variant="outline"
                onClick={() => navigate('/dashboard/clientes')}
                disabled={loading}
              >
                Cancelar
              </Button>
              <Button
                type="submit"
                disabled={loading}
              >
                {loading ? (
                  <>
                    <Loader2 className="h-4 w-4 mr-2 animate-spin" />
                    Salvando...
                  </>
                ) : (
                  <>
                    <Save className="h-4 w-4 mr-2" />
                    Salvar Altera√ß√µes
                  </>
                )}
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  )
}
