import { supabase } from './supabase'

export interface SystemConfig {
  id: string
  config_key: string
  config_value: string
  description?: string
  created_at: string
  updated_at: string
}

// Fun√ß√£o para obter uma configura√ß√£o espec√≠fica
export async function getSystemConfig(key: string): Promise<string | null> {
  try {
    const { data, error } = await supabase
      .from('system_config')
      .select('config_value')
      .eq('config_key', key)
      .single()

    if (error) {
      console.error('Erro ao buscar configura√ß√£o:', error)
      // Se a tabela n√£o existe ou h√° erro de permiss√£o, retornar valor padr√£o
      if (error.code === 'PGRST116' || error.message.includes('relation "system_config" does not exist')) {
        console.warn('Tabela system_config n√£o existe. Retornando valor padr√£o.')
        return getDefaultConfigValue(key)
      }
      return null
    }

    return data?.config_value || null
  } catch (error) {
    console.error('Erro ao buscar configura√ß√£o:', error)
    return getDefaultConfigValue(key)
  }
}

// Fun√ß√£o para obter valores padr√£o quando a tabela n√£o existe
function getDefaultConfigValue(key: string): string {
  const defaults: Record<string, string> = {
    'rh_nova_vaga_enabled': 'false',
    'rh_edit_enabled': 'false',
    'rh_delete_enabled': 'false'
  }
  return defaults[key] || 'false'
}

// Fun√ß√£o para obter todas as configura√ß√µes
export async function getAllSystemConfigs(): Promise<SystemConfig[]> {
  try {
    const { data, error } = await supabase
      .from('system_config')
      .select('*')
      .order('config_key')

    if (error) {
      throw new Error(error.message)
    }

    return data || []
  } catch (error) {
    console.error('Erro ao buscar configura√ß√µes:', error)
    return []
  }
}

// Fun√ß√£o para atualizar uma configura√ß√£o
export async function updateSystemConfig(key: string, value: string): Promise<boolean> {
  try {
    // Primeiro, verificar se a configura√ß√£o j√° existe
    const { data: existingData, error: selectError } = await supabase
      .from('system_config')
      .select('id')
      .eq('config_key', key)
      .single()

    if (selectError && selectError.code !== 'PGRST116') {
      console.error('Erro ao verificar configura√ß√£o existente:', selectError)
    }

    let error;
    if (existingData) {
      // Se existe, fazer UPDATE
      const { error: updateError } = await supabase
        .from('system_config')
        .update({
          config_value: value,
          updated_at: new Date().toISOString()
        })
        .eq('config_key', key)
      
      error = updateError;
    } else {
      // Se n√£o existe, fazer INSERT
      const { error: insertError } = await supabase
        .from('system_config')
        .insert({
          config_key: key,
          config_value: value,
          updated_at: new Date().toISOString()
        })
      
      error = insertError;
    }

    if (error) {
      console.error('Erro ao atualizar configura√ß√£o:', error)
      // Se a tabela n√£o existe, mostrar mensagem espec√≠fica
      if (error.code === 'PGRST116' || error.message.includes('relation "system_config" does not exist')) {
        console.error('ERRO: Tabela system_config n√£o existe. Execute a migra√ß√£o do banco de dados primeiro.')
        throw new Error('Tabela system_config n√£o existe. Execute a migra√ß√£o do banco de dados primeiro.')
      }
      throw new Error(`Erro ao atualizar configura√ß√£o: ${error.message}`)
    }

    return true
  } catch (error) {
    console.error('Erro ao atualizar configura√ß√£o:', error)
    throw error
  }
}

// Fun√ß√£o para criar configura√ß√£o se n√£o existir
export async function createSystemConfigIfNotExist(key: string, defaultValue: string, description?: string): Promise<boolean> {
  try {
    // Verificar se j√° existe
    const { data: existingData, error: selectError } = await supabase
      .from('system_config')
      .select('id')
      .eq('config_key', key)
      .single()

    // Se n√£o existe erro de "n√£o encontrado", significa que n√£o existe
    if (selectError && selectError.code === 'PGRST116') {
      // Inserir nova configura√ß√£o
      const { error: insertError } = await supabase
        .from('system_config')
        .insert({
          config_key: key,
          config_value: defaultValue,
          description: description || '',
          created_at: new Date().toISOString(),
          updated_at: new Date().toISOString()
        })

      if (insertError) {
        console.error('Erro ao criar configura√ß√£o:', insertError)
        return false
      }
      
      console.log(`Configura√ß√£o ${key} criada com valor padr√£o: ${defaultValue}`)
      return true
    } else if (selectError) {
      console.error('Erro ao verificar configura√ß√£o existente:', selectError)
      return false
    } else {
      // J√° existe, n√£o fazer nada
      console.log(`Configura√ß√£o ${key} j√° existe`)
      return true
    }
  } catch (error) {
    console.error('Erro ao criar configura√ß√£o:', error)
    return false
  }
}

// Fun√ß√£o espec√≠fica para verificar se RH pode acessar Nova Oportunidade
export async function isRHNovaVagaEnabled(): Promise<boolean> {
  const value = await getSystemConfig('rh_nova_vaga_enabled')
  return value === 'true'
}

// Fun√ß√£o espec√≠fica para verificar se RH pode editar vagas
export async function isRHEditEnabled(): Promise<boolean> {
  const value = await getSystemConfig('rh_edit_enabled')
  return value === 'true'
}

// Fun√ß√£o espec√≠fica para verificar se RH pode deletar vagas
export async function isRHDeleteEnabled(): Promise<boolean> {
  const value = await getSystemConfig('rh_delete_enabled')
  return value === 'true'
}

// Fun√ß√£o para habilitar/desabilitar acesso RH √† Nova Oportunidade
export async function setRHNovaVagaAccess(enabled: boolean): Promise<boolean> {
  return await updateSystemConfig('rh_nova_vaga_enabled', enabled.toString())
}

// Fun√ß√£o para habilitar/desabilitar edi√ß√£o RH
export async function setRHEditAccess(enabled: boolean): Promise<boolean> {
  return await updateSystemConfig('rh_edit_enabled', enabled.toString())
}

// Fun√ß√£o para habilitar/desabilitar exclus√£o RH
export async function setRHDeleteAccess(enabled: boolean): Promise<boolean> {
  return await updateSystemConfig('rh_delete_enabled', enabled.toString())
}
